import { mapState, mapActions } from 'vuex'
import Team from './Team.vue'
export default {
  comonents: {
    Team,
  },
  props: {
    id: {
      type: String,
      default: () => '',
    },
  },
  data: () => ({
    tab: 'features',
    areFeatures: true,
    areAccessories: true,
    areConfig: true,
    areVideos: true,
    tutorials: '',
    ourTeam: [
      {
        name: 'Andy Sibun',
        position: 'Technical Manager',
        phone: '01572 898515',
        email: 'andy.sibun@cbsproducts.com',
        photo: '',
      },
    ],
  }),
  computed: {
    ...mapState(['product']),
    product() {
      return this.$store.state.product
    },
  },
  created() {
    this.getIdProduct()
  },
  methods: {
    ...mapActions(['getProduct']),
    getIdProduct() {
      this.$store.dispatch('getProduct', this.id)
    },
    isMobile() {
      if (screen.width <= 760) {
        return '60vh'
      } else {
        return '100vh'
      }
    },
    showAccessories() {
      this.data.accesories === []
        ? (this.areAccessories = false)
        : (this.areAccessories = true)

      return this.areAccessories
    },
  },
}
